:root {
  --font-body: "Space Grotesk", sans-serif;
  --font-display: "Fraunces", serif;
  --bg-0: #fff7eb;
  --bg-1: #ffe2bf;
  --panel: rgba(255, 255, 255, 0.78);
  --ink: #102028;
  --ink-soft: #35505b;
  --accent: #d1542d;
  --accent-soft: #f28f3b;
  --ok: #2f7a48;
  --muted: #8ea0a8;
  --border: rgba(16, 32, 40, 0.12);
}

* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100%;
}

body {
  font-family: var(--font-body);
  color: var(--ink);
  background:
    radial-gradient(circle at 15% 15%, rgba(242, 143, 59, 0.35), transparent 30%),
    radial-gradient(circle at 85% 8%, rgba(209, 84, 45, 0.25), transparent 28%),
    linear-gradient(145deg, var(--bg-0), var(--bg-1));
}

h1,
h2,
h3,
h4 {
  font-family: var(--font-display);
  margin: 0;
  line-height: 1.1;
}

p {
  margin: 0;
  color: var(--ink-soft);
}

.shell {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 1rem 4rem;
  display: grid;
  gap: 1rem;
}

.hero {
  background: linear-gradient(130deg, rgba(255, 255, 255, 0.9), rgba(255, 247, 235, 0.85));
  border: 1px solid var(--border);
  border-radius: 24px;
  padding: 1.5rem;
}

.hero h1 {
  font-size: clamp(2rem, 5vw, 4rem);
  margin: 0.5rem 0;
}

.eyebrow {
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-size: 0.72rem;
  color: var(--accent);
}

.panel {
  background: var(--panel);
  border: 1px solid var(--border);
  border-radius: 20px;
  padding: 1rem;
  backdrop-filter: blur(8px);
}

.grid-two {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

.list {
  list-style: none;
  margin: 0.8rem 0 0;
  padding: 0;
  display: grid;
  gap: 0.6rem;
}

.tile {
  border: 1px solid var(--border);
  background: rgba(255, 255, 255, 0.88);
  border-radius: 14px;
  padding: 0.75rem;
}

.task-item h4,
.place-item h4 {
  margin: 0.2rem 0;
}

.mode-panel {
  background: linear-gradient(145deg, rgba(16, 32, 40, 0.96), rgba(21, 54, 65, 0.94));
}

.mode-panel .eyebrow,
.mode-panel h2,
.mode-panel p,
.mode-panel .mode-tag {
  color: #f5f8f9;
}

.mode-tag {
  margin-top: 0.9rem;
  width: fit-content;
  padding: 0.3rem 0.6rem;
  border-radius: 999px;
  border: 1px solid rgba(255, 255, 255, 0.35);
  font-size: 0.8rem;
}

.categories {
  margin-top: 0.8rem;
  display: grid;
  gap: 0.75rem;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
}

.category-block {
  border: 1px solid var(--border);
  border-radius: 16px;
  padding: 0.7rem;
  background: rgba(255, 255, 255, 0.85);
}

.list.compact {
  margin-top: 0.4rem;
}

.place-item {
  position: relative;
}

.status {
  position: absolute;
  top: 0.7rem;
  right: 0.7rem;
  font-size: 0.68rem;
  text-transform: uppercase;
  letter-spacing: 0.07em;
  padding: 0.18rem 0.35rem;
  border-radius: 999px;
}

.status.open {
  color: var(--ok);
  background: rgba(47, 122, 72, 0.1);
}

.status.closed {
  color: var(--muted);
  background: rgba(142, 160, 168, 0.18);
}

.reveal {
  opacity: 0;
  transform: translateY(14px);
  animation: revealUp 0.6s ease forwards;
}

.delay-1 {
  animation-delay: 0.1s;
}

.delay-2 {
  animation-delay: 0.2s;
}

.delay-3 {
  animation-delay: 0.3s;
}

@keyframes revealUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 640px) {
  .shell {
    padding: 1rem 0.75rem 2.5rem;
  }

  .hero,
  .panel {
    border-radius: 16px;
  }
}
